.nav-container{
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 15vh;
}

.page-button{
    outline:0; 
    font-family: 'Inconsolata', monospace;
    font-size: 1em;
    background: whitesmoke;
    color: black;
    border:none;
    border-radius: 10px;
    padding:1em 2em;
    transition:all .3s ease-out; 
    box-shadow:inset 0 -8px 0 0 rgb(160, 160, 160),
      1px 1px 0 0 rgba(0,0,0, 0.1),
      2px 2px 0 0 rgba(0,0,0, 0.1),
      3px 3px 0 0 rgba(0,0,0, 0.1),
      4px 4px 0 0 rgba(0,0,0, 0.1),
      5px 5px 0 0 rgba(0,0,0, 0.1),
      6px 6px 0 0 rgba(0,0,0, 0.1),
      7px 7px 0 0 rgba(0,0,0, 0.1),
      8px 8px 0 0 rgba(0,0,0, 0.1),
      9px 9px 0 0 rgba(0,0,0, 0.1);

      &:active{
        color:#888;
        box-shadow:inset 0 -4px 0 0 rgb(160, 160, 160),
          1px 1px 0 0 rgba(0,0,0, 0.1),
          2px 2px 0 0 rgba(0,0,0, 0.1),
          4px 4px 0 0 rgba(0,0,0, 0.1),
      }

      &:hover{
        cursor: pointer;
      }
  }


@media (max-width: 640px) {
  .short-text { display: inline-block; }
  .full-text { display: none; }

  .page-button{
      font-size: 1em;
      padding: 0.5em, 0.5em;
    }
}
 